import * as React from "react";
import HeaderSlider from "./components/HeaderSlider";
import Jumbotron from "./components/Jumbotron";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";

import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import Container from "@material-ui/core/Container";
import "./index.css";
import profileImg from "./../images/Avatar-1.jpg";
import Skill from "./components/Skill";
import Navigation from "./components/Navigation";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.secondary,
  },
}));

// markup
const IndexPage = () => {
  const classes = useStyles();
  return (
    <>
      <Jumbotron type="splat-toggle back-row-toggle" />

      <Box
        style={{
          background:
            "linear-gradient(120deg, rgb(24, 24, 28), rgb(63, 81, 181))",
        }}
      >
        <Container style={{ marginTop: -100 }}>
          <Grid container spacing={3}>
            <Grid item xs={4}>
              <img
                src={profileImg}
                style={{
                  width: "80%",
                  maxWidth: 400,
                  margin: "auto",
                  zIndex: 999,
                }}
                alt="Oliver Tembo"
              />
            </Grid>
            <Grid item xs={8}>
              <Navigation />

              <Paper className={classes.paper} style={{ marginTop: "20px" }}>
                <Typography variant="h3" style={{ textAlign: "center" }}>
                  Oliver Tembo
                </Typography>

                <Typography variant="h5" style={{ textAlign: "center" }}>
                  Full Stack Engineer
                </Typography>
                <Typography variant="p" style={{ textAlign: "center" }}>
                  Web, Augmented Reality, Virual Reality
                </Typography>
              </Paper>
            </Grid>
          </Grid>
        </Container>

        <Container>
          <Grid container spacing={3}>
            <Grid item xs={3}>
              <Skill>
                <div className="bar bar6">
                  <span>PHP Core</span>
                </div>

                <div className="bar bar2">
                  <span>Laravel</span>
                </div>
                <div className="bar bar3">
                  <span>Wordpress</span>
                </div>
                <div className="bar bar4">
                  <span>CodeIgniter</span>
                </div>
              </Skill>
            </Grid>
            <Grid item xs={3}>
              <Skill
                language="React.js"
                subtitle="Magical"
                info=""
                experience="4+ years"
              >
                <div className="bar bar1">
                  <span>Redux</span>
                </div>
              </Skill>
            </Grid>
            <Grid item xs={3}>
              <Skill
                language="React Native"
                subtitle="Magical"
                info=""
                experience="4+ years"
              >
                <div className="bar bar4">
                  <span>CodeIgniter</span>
                </div>
              </Skill>
            </Grid>
            <Grid item xs={3}>
              <Skill
                language="Node.js"
                subtitle="Magical"
                info=""
                experience="4+ years"
              />
            </Grid>
            <Grid item xs={3}>
              <Skill
                language="CSS"
                subtitle="Can Align Element"
                info=""
                experience="4+ years"
              />
            </Grid>
            <Grid item xs={3}>
              <Skill
                language="Typescript"
                subtitle="Very Strict"
                info="like my 5th grade teacher"
                experience="4+ years"
              />
            </Grid>
            <Grid item xs={3}>
              <Skill
                language="C++"
                subtitle="fundamental"
                info=""
                experience="4+ years"
              />
            </Grid>
            <Grid item xs={3}>
              <Skill
                language="Ruby"
                subtitle="Rails"
                info=""
                experience="4+ years"
              />
            </Grid>
          </Grid>
        </Container>
      </Box>
    </>
  );
};

export default IndexPage;
